@import url(https://fonts.googleapis.com/css2?family=Mali:wght@300&display=swap);
.body {
  max-width: 400px;
  margin: auto;
  text-align: center;
  color: #00adb5;
}
.app {
  font-family: "Mali", cursive;
  background-color: #222831;
  height: 650px;
  padding: 40px;
  border-radius: 20px;
}

.search-bar {
  width: 100%;
  text-align: center;
  margin-bottom: 2rem;
  height: 2rem;
  font-family: "Mali", cursive;
}

.poke-name {
  font-size: 2rem;
}

.img-container {
  background-color: #eeeeee;
  border-radius: 10px;
}

.sprite-img {
  min-width: 50%;
}

